import React from "react";
import getImage from "./Icons";

const Card = ({ card, handleCardClick, index, previousSelected }) => {
  return (
    <div
      className={card.isFlipped ? "memory-card flip" : "memory-card"}
      onClick={card.isMatched ? null : () => handleCardClick(index)}
    >
      <img
        // className={card.isMatched ? "front-face matched" : "front-face"}
        className={
          !previousSelected
            ? card.isMatched
              ? "front-face matched"
              : "front-face wrong"
            : card.isMatched
            ? "front-face matched"
            : "front-face"
        }
        src={getImage(card.img)}
        alt={card.img}
      />
      <img className="back-face" src={getImage("backImg")} alt="back-img" />
    </div>
  );
};

export default Card;
